<template>
  <div class="row" v-if="store">
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width my-0 flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Add order</h5>
          </div>
          <div class="flex-align-center sm-hide flex-end">
            <el-button
              @click="$router.go(-1)"
              type="primary"
              v-ripple.400
              class="flat my-0"
            >
              <i class="bx bx-x mr-1"></i>Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              class="my-0"
              type="primary"
            >
              <i class="bx bx-save mr-1"></i>Save
            </el-button>
          </div>
        </div>

        <div class="col-lg-8 col-md-cu-8 pr-lg-0">
          <div class="box">
            <div class="flex-x-between">
              <p class="mt-0 mb-2 text-bold">Items</p>

              <div class="d-flex align-items-center">
                <p
                  @click="addItem"
                  class="mt-0 pointer primary hover-underline"
                >
                  <i class="bx bx-plus mr-1"></i>Add item
                </p>
              </div>
            </div>
            <div class="row">
              <div class="col-12" v-if="store && store.type == 'normal'">
                <el-select
                  v-model="productQuery"
                  filterable
                  remote
                  reserve-keyword
                  class="w-100 my-3 my-sm-2"
                  placeholder="search product"
                  :remote-method="getProducts"
                  :loading="loadingProduct"
                >
                  <el-option
                    v-for="product in products"
                    :key="product.id"
                    :label="product.name + ' / ' + product.barcode || ''"
                    :value="product"
                  >
                  </el-option>
                </el-select>
              </div>
              <div
                class="col-12"
                v-if="sale.products.length > 0"
                v-loading="loadingProduct"
              >
                <el-collapse>
                  <el-collapse-item
                    v-for="(product, index) in sale.products"
                    :key="index + product.name + product.image"
                    :name="index"
                  >
                    <template slot="title">
                      <div class="flex-x-between w-100 collapse-header">
                        <p>
                          {{ product.name }} - {{ product.subtotal
                          }}{{ store.type === "supplier" ? "¥" : "MMK" }}
                        </p>
                        <p
                          @click="removeProduct(index)"
                          class="danger hover-underline"
                          style="margin-right: 8px; z-index: 10"
                        >
                          <i class="bx bx-x"></i>
                        </p>
                      </div>
                    </template>
                    <div class="row mb-3">
                      <div class="col-12 mb-3">
                        <label for="Media" class="custom-label">Media</label>
                        <file
                          :key="index + product.name"
                          @completed="setImage($event, index)"
                          :initialData="
                            product.image ? [{ image: product.image }] : []
                          "
                        />
                      </div>
                      <div class="col-12 mb-3">
                        <label for="Name" class="custom-label">Name</label>
                        <el-input
                          class="w-100"
                          name="Name"
                          v-model="product.name"
                        ></el-input>
                      </div>

                      <div
                        class="col-lg-6 mb-3"
                        v-if="store.type !== 'supplier'"
                      >
                        <label for="Link" class="custom-label"
                          >Capital price(MMK)</label
                        >
                        <el-input
                          class="w-100"
                          name="Capital price(MMK)"
                          @change="calcPrice(index)"
                          type="number"
                          v-model="product.primary_price_myanmar"
                        ></el-input>
                      </div>
                      <div
                        class="col-lg-6 mb-3"
                        v-if="store.type !== 'supplier'"
                      >
                        <label for="Link" class="custom-label"
                          >Selling Price(MMK)</label
                        >
                        <el-input
                          class="w-100"
                          name="Sale Price(MMK)"
                          @change="calcPrice(index)"
                          type="number"
                          v-model="product.sale_price"
                        ></el-input>
                      </div>

                      <div class="col-lg-6">
                        <div class="row">
                          <div class="col-6">
                            <label for="Link" class="custom-label"
                              >Quantity</label
                            >
                            <el-input-number
                              :min="0"
                              class="w-100"
                              controls-position="right"
                              v-model="product.quantity"
                              @change="changeQuantity(index)"
                            ></el-input-number>
                          </div>
                          <div class="col-6">
                            <label for="Link" class="custom-label"
                              >Fulfilled</label
                            >
                            <el-input-number
                              :min="0"
                              class="w-100"
                              controls-position="right"
                              v-model="product.number_of_fullfilled"
                            ></el-input-number>
                          </div>
                        </div>
                      </div>
                      <div
                        class="col-lg-6 mb-3"
                        v-if="store.type == 'supplier'"
                      >
                        <label for="Link" class="custom-label">Price</label>
                        <el-input
                          class="w-100"
                          name="Price"
                          @change="calcPrice(index)"
                          type="number"
                          v-model="product.sale_price"
                        ></el-input>
                      </div>
                      <div
                        v-if="store.type !== 'supplier'"
                        class="
                          d-flex
                          justify-content-around
                          col-lg-6
                          align-items-center
                        "
                      >
                        <div
                          class="
                            d-flex
                            justify-content-center
                            align-items-center
                            flex-column
                          "
                        >
                          <p class="md-text">Margin</p>
                          <p class="mt-2">
                            {{ product.margin ? product.margin + "%" : "-" }}
                          </p>
                        </div>
                        <div
                          class="
                            d-flex
                            justify-content-center
                            align-items-center
                            flex-column
                          "
                        >
                          <p class="md-text">Profit(MMK)</p>
                          <p class="mt-2">
                            {{ product.profit ? product.profit : "-" }}
                          </p>
                        </div>
                      </div>
                    </div>
                  </el-collapse-item>
                </el-collapse>
              </div>
              <el-empty class="w-100" description="No item found!" v-else>
                <template #image> <empty /> </template
              ></el-empty>
            </div>
          </div>

          <div class="box mt-3 my-sm-3 mb-3">
            <div class="row">
              <div class="col-12">
                <p class="mt-0 mb-2 text-bold">Pricing</p>
                <div class="flex-x-between">
                  <p
                    class="primary hover-underline pointer"
                    @click="openDiscount = !openDiscount"
                  >
                    Add discount
                  </p>

                  <p class="text-right">
                    {{
                      parseFloat(sale.discount) === 0
                        ? "-"
                        : "-" + sale.discount + "MMK"
                    }}
                    <span v-if="sale.discount_reason" class="sm-text">
                      <br />
                      {{ sale.discount_reason }}
                    </span>
                  </p>
                </div>
                <el-divider></el-divider>
                <div class="flex-x-between">
                  <p class="">Subtotal</p>
                  <p>
                    {{
                      parseFloat(sale.subtotal) === 0
                        ? "-"
                        : sale.subtotal + "MMK"
                    }}
                  </p>
                </div>
                <el-divider></el-divider>

                <div class="flex-x-between">
                  <p class="">Total</p>
                  <p class="">
                    {{
                      parseFloat(sale.total) === 0 ? "-" : sale.total + "MMK"
                    }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-4 col-md-cu-4">
          <div class="box">
            <label for="" class="custom-label md-text">Date of order</label>
            <el-date-picker
              class="w-100"
              v-model="sale.date"
              type="date"
              format="dd/MM/yyyy"
              value-format="dd/MM/yyyy"
              placeholder="Pick a date"
            >
            </el-date-picker>
            <label
              for=""
              v-if="store.type !== 'supplier'"
              class="custom-label mt-3 md-text"
              >Type</label
            >
            <div class="flex-align-center" v-if="store.type !== 'supplier'">
              <el-radio v-model="sale.type" class="mr-3" label="In Stock"
                >In Stock</el-radio
              >
              <el-radio v-model="sale.type" label="Pre Order"
                >Pre Order</el-radio
              >
            </div>

            <label for="" class="custom-label mt-3 md-text">Exported By</label>
            <el-select
              v-model="sale.exported_by"
              filterable
              remote
              reserve-keyword
              class="w-100"
              placeholder="Select"
              :remote-method="getEmployees"
              :loading="loadingEmployee"
            >
              <el-option
                v-for="employee in employees"
                :key="employee.id"
                :label="employee.name"
                :value="employee.name"
              >
              </el-option>
            </el-select>

            <label for="" class="custom-label mt-3 md-text">Delivery</label>
            <el-select
              v-model="sale.delivery_company_name"
              filterable
              remote
              reserve-keyword
              class="w-100"
              allow-create
              placeholder="Select or create new"
              :remote-method="getDeliveries"
              :loading="loadingDelivey"
            >
              <el-option
                v-for="delivery in deliveries"
                :key="delivery.id"
                :label="delivery.name"
                :value="delivery.name"
              >
              </el-option>
            </el-select>

            <label class="custom-label mt-3 mb-0">Note</label>
            <el-input
              type="textarea"
              placeholder="Text such as facebook profile link, etc."
              :row="5"
              v-model="sale.note"
            ></el-input>
          </div>
          <div class="box mt-3" v-if="store.type == 'supplier'">
            <p class="mt-0 mb-2 text-bold">Pricing Details</p>
            <label class="custom-label">Exchange Rate</label>
            <el-input class="w-100" v-model="sale.money_price"></el-input>

            <label class="custom-label mt-3">Service Fees(%)</label>
            <el-input
              class="w-100"
              @change="calcTotal"
              v-model="sale.supplier_percentage"
            ></el-input>
          </div>

          <div class="box mt-3 mb-3">
            <p class="mt-0 mb-2 text-bold">Customer</p>
            <label for="" class="custom-label md-text">Name</label>
            <el-select
              v-model="sale.customer_name"
              @change="changeCustomer"
              filterable
              remote
              data-is-search="true"
              allow-create
              reserve-keyword
              class="w-100 mb-3"
              placeholder="Select or create new"
              :remote-method="getCustomers"
              :loading="loadingCustomer"
            >
              <el-option
                v-for="customer in customers"
                :key="customer.id"
                :label="customer.name"
                :value="customer.name"
              >
              </el-option>
            </el-select>

            <label for="" class="custom-label md-text">Phone</label>
            <el-input
              class="w-100 mb-3"
              placeholder="Phone number"
              v-model="sale.phone"
            ></el-input>

            <label for="" class="custom-label md-text">State & city</label>
            <el-cascader
              v-model="address"
              :options="options"
              @change="handleChangeAddress"
            ></el-cascader>
            <label for="Street address" class="mt-3 custom-label"
              >Street address</label
            >
            <el-input
              class="w-100"
              name="Street address"
              placeholder="Street address"
              v-model="sale.address.address"
            ></el-input>
          </div>
        </div>

        <div class="col-12 lg-hide mb-3 md-hide">
          <el-button
            :disabled="!startEditing"
            :loading="saving"
            v-ripple.400
            @click="Post"
            class="my-0 mb-2 w-100"
            type="primary"
          >
            <i class="bx bx-save mr-1"></i>Save
          </el-button>
          <el-button
            @click="$router.go(-1)"
            type="primary"
            v-ripple.400
            class="flat w-100 mb-2 mx-0 my-0"
          >
            <i class="bx bx-x mr-1"></i>Discard
          </el-button>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openDiscount"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Add discount</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row">
          <div class="col-6 pr-1">
            <label for="discountType" class="pt-1 custom-label"
              >Discount type</label
            >
            <el-select
              v-model="sale.discount_type"
              class="w-100"
              name="discountType"
            >
              <el-option label="Amount" value="$"> </el-option>
              <el-option label="Percentage" value="%"> </el-option>
            </el-select>
          </div>
          <div class="col-6 pl-1">
            <label for="discountType" class="pt-1 custom-label"
              >Discount value</label
            >
            <el-input type="number" class="w-100" v-model="discount"></el-input>
          </div>
          <div class="col-12">
            <label for="reason" class="pt-1 custom-label">Reason</label>
            <el-input
              type="textarea"
              :row="3"
              class="w-100"
              name="reason"
              v-model="sale.discount_reason"
            ></el-input>
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openDiscount = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            @click="calcDiscount"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import address from "../../../core/constant/address";
import { url, mediaUrl } from "../../../core/constant/data";
import { file, empty } from "../../common";

export default {
  data() {
    return {
      url: url,
      productQuery: "",
      openCustomProduct: false,
      mediaUrl: mediaUrl,
      options: address,
      customProduct: {
        name: "",
        is_custom_product: true,
        quantity: "1",
        price: "0",
        number_of_stock: 100000,
        image: "images/default.png",
        variation_product: null,
        product: null,
        subtotal: "0",
      },
      address: [],
      customers: [],
      deliveries: [],
      inventory: [],
      loadingProduct: false,
      loadingCustomer: false,
      loadingDelivey: false,
      tax: 5,
      discount: 0,
      selectedProduct: "",
      openDiscount: false,
      saving: false,
      sale: {
        payment_status: "Unpaid",
        supplier_percentage: "5",
        money_price: "300",
        customer_name: "",
        location_name: "",
        status: "Active",
        tax_rate: 5,
        delivery_company_name: null,
        delivery_date: null,
        payment_method: null,
        type: "In Stock",
        exported_by: "",
        location: 1,
        payments: [],
        address: { state: "", city: "", address: "", map: "" },
        products: [],
        timelines: [],
        subtotal: "",
        total: "0",
        discount: "0",
        discount_reason: "",
        discount_type: "$",
        is_same_address: false,
        tax: "",
        note: "note",
        tags: [],
        phone: "",
        date: "",
      },
      selectedCustomer: {},
      loading: null,
      startEditing: false,
      activePayment: false,
      adding: false,
      due_amount: 0,
      query: "",
      products: [],
      loadingEmployee: false,
      employees: [],
    };
  },
  components: { file, empty },
  watch: {
    sale: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
    productQuery(val) {
      this.sale.products.push({
        quantity: "1",
        sale_price: val.price,
        subtotal: val.price,
        name: val.name,
        image: val.image,
        primary_price: "0",
        primary_price_myanmar: val.cost_per_item,
        number_of_fullfilled: 0,
        product_id: val.id,
      });
    },
    locations: {
      handler: function (value) {
        this.sale.location = value[0].id;
        this.sale.location_name = value[0].name;
      },
      deep: true,
    },
  },
  mounted() {
    setTimeout(() => {
      this.startEditing = false;
    }, 100);
    this.sale.date = this.$moment().format("DD/MM/YYYY");
    this.$store.commit("setIsReady", true);
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
    document.addEventListener("scan", this.onScan);
    this.$once("hook:beforeDestroy", () => {
      document.removeEventListener("scan", this.onScan);
    });
  },
  beforeDestroy() {
    document.removeEventListener("scan", this.onScan);
  },
  beforeRouteLeave(to, from, next) {
    document.removeEventListener("scan", this.onScan);
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    onScan({ detail }) {
      this.loadingProduct = true;
      if (detail.scanCode) {
        this.$axios
          .get("product/search/?query=" + detail.scanCode)
          .then((response) => {
            this.loadingProduct = false;

            if (response.data.length > 0) {
              var val = response.data[0];
              var check = this.sale.products.filter(
                (item) => item.product_id == val.id
              );

              if (check.length > 0) {
                this.sale.products.map((item) => {
                  if (item.product_id == val.id) {
                    item.quantity = item.quantity + 1;
                  }
                });
              } else {
                this.sale.products.push({
                  quantity: "1",
                  sale_price: val.price,
                  subtotal: val.price,
                  name: val.name,
                  image: val.image,
                  primary_price: "0",
                  primary_price_myanmar: val.cost_per_item,
                  number_of_fullfilled: 0,
                  product_id: val.id,
                });
              }
            } else {
              this.$noti.display({
                message: "No products found with " + detail.scanCode,
                title: "",
                status: "success",
                color: "error",
              });
            }
          })
          .catch((err) => {
            this.loadingProduct = false;
            this.$noti.display({
              message: err,
              title: "",
              status: "error",
              color: "error",
            });
          });
      }
    },
    getEmployees(query) {
      this.loadingEmployee = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-employee/?query=" + query)
        .then((response) => {
          this.loadingEmployee = false;
          this.employees = response.data;
        })
        .catch((err) => {
          this.loadingEmployee = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    handleChangeAddress(address) {
      this.sale.address.state = address[0];
      this.sale.address.city = address[1];
    },
    setImage($event, index) {
      if ($event.length > 0) {
        this.sale.products[index].image = $event[0].image;
      }
    },
    addItem() {
      this.sale.products.push({
        quantity: "1",
        sale_price: "",
        subtotal: "",
        name: "",
        image: "/media/default.png",
        link: null,
        primary_price: null,
        primary_price_myanmar: null,
        number_of_fullfilled: 0,
        product_id: "",
      });
      this.calcSubtotal();
    },
    removeProduct(index) {
      this.sale.products.splice(index, 1);
      this.calcSubtotal();
    },
    getCustomers(query) {
      this.loadingCustomer = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-customer/?query=" + query)
        .then((response) => {
          this.loadingCustomer = false;
          this.customers = response.data;
        })
        .catch((err) => {
          this.loadingCustomer = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    getDeliveries(query) {
      this.loadingDelivery = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-delivery/?query=" + query)
        .then((response) => {
          this.loadingDelivery = false;
          this.deliveries = response.data;
        })
        .catch((err) => {
          this.loadingDelivery = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    getProducts(query) {
      this.loadingProduct = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("product/search/?query=" + query)
        .then((response) => {
          this.loadingProduct = false;
          this.products = response.data;
        })
        .catch((err) => {
          this.loadingProduct = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeCustomer() {
      var check = this.customers.filter((data) => {
        return data.name == this.sale.customer_name;
      });
      if (check.length > 0) {
        this.selectedCustomer = check[0];
        if (this.selectedCustomer.phone) {
          this.sale.phone = this.selectedCustomer.phone;
        }
        if (this.selectedCustomer.address) {
          this.sale.address = this.selectedCustomer.address;
          this.address = [
            this.selectedCustomer.address.state,
            this.selectedCustomer.address.city,
          ];
        }
      }
    },
    calcDiscount() {
      if (this.sale.discount_type === "%") {
        this.sale.discount =
          (parseFloat(this.discount) / 100) * parseFloat(this.sale.subtotal);
      } else {
        this.sale.discount = parseFloat(this.discount);
      }
      this.openDiscount = false;
      this.calcTotal();
    },
    calcTotal() {
      if (this.store.type === "supplier") {
        this.sale.total = (
          parseFloat(this.sale.subtotal) +
          parseFloat(this.sale.subtotal) *
            (parseFloat(this.sale.supplier_percentage) / 100) -
          parseFloat(this.sale.discount)
        ).toFixed(0);
      } else {
        this.sale.total = (
          parseFloat(this.sale.subtotal) - parseFloat(this.sale.discount)
        ).toFixed(0);
      }
    },
    calcSubtotal() {
      this.sale.subtotal = this.sale.products
        .reduce((total, item) => {
          return total + parseFloat(item.subtotal);
        }, 0)
        .toFixed(2);
      this.calcDiscount();
    },
    changeQuantity(index) {
      this.sale.products[index].subtotal = (
        parseFloat(this.sale.products[index].sale_price) *
        parseFloat(this.sale.products[index].quantity)
      ).toFixed(2);
      this.calcSubtotal();
    },
    calcPrice(index) {
      if (
        this.sale.products[index].sale_price &&
        this.sale.products[index].primary_price_myanmar
      ) {
        this.sale.products[index].profit = parseFloat(
          this.sale.products[index].sale_price -
            this.sale.products[index].primary_price_myanmar
        );
        this.sale.products[index].margin = (
          (this.sale.products[index].profit /
            this.sale.products[index].sale_price) *
          100
        ).toFixed(2);
      } else {
        this.sale.products[index].margin = null;
        this.sale.products[index].profit = null;
      }
      this.changeQuantity(index);
    },
    calcAllProductPrice() {
      var data = [];
      this.sale.products.forEach((product, index) => {
        product.primary_price_myanmar = (
          (parseFloat(product.primary_price) *
            (parseFloat(this.sale.supplier_percentage) / 100) +
            parseFloat(product.primary_price)) *
          this.sale.money_price
        ).toFixed(0);
        this.calcPrice(index);
        data.push(product);
      });
      var dataString = JSON.stringify(data);
      setTimeout(() => {
        this.sale.products = [];
        this.sale.products = JSON.parse(dataString);
      }, 500);
    },
    changePrimaryPrice($event, index) {
      const supplier_percentage =
        parseFloat(this.sale.supplier_percentage) / 100;
      this.sale.products[index].primary_price_myanmar = parseFloat(
        (parseFloat($event) * supplier_percentage + parseFloat($event)) *
          this.sale.money_price
      );
      var string = JSON.stringify(this.sale.products);
      this.sale.products = [];
      this.sale.products = JSON.parse(string);
      this.calcPrice(index);
    },
    Post() {
      this.saving = true;
      this.sale.store = this.store.id;
      this.$axios
        .post("sale/", { data: this.sale })
        .then((res) => {
          this.startEditing = false;
          this.saving = false;
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Sale has been successfully added.",
          });
          this.$router.push({
            name: "Edit order",
            params: { id: res.data.id },
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeIsSame() {
      if (this.sale.is_same_address) {
        this.sale.shipping_address = this.sale.address;
      } else {
        this.sale.shipping_address = {
          state: "",
          city: "",
          address: "",
          map: "",
        };
      }
    },
  },
  computed: {
    ...mapState(["isReady", "locations", "store"]),
  },
};
</script>

